import React, { useContext, useRef, useState } from 'react';
import { DefaultContext } from '../contexts/DefaultContext';
import { Button, Col, Form, Row } from 'react-bootstrap';
import HeatCalc from './HeatCalc';
import { useNavigate } from 'react-router-dom';

export default function Final() {
  const {
    airCalc,
    eGap,
    gObl,
    gU,
    handleInsThickness,
    handleSecondInsThickness,
    handleVaporMembraneAir,
    handlePlasterValue,
    handleVaporMembraneR,
    handleVentHeight,
    handleVentIn,
    handleVentMed,
    handleVentOut,
    insThickness,
    plaster,
    outE,
    rObl,
    rRed,
    secondIns,
    secondInsThickness,
    vaporCalc,
    vaporMembraneAir,
    vaporMembraneR,
    ventHeight,
    ventIn,
    ventMed,
    ventOut,
  } = useContext(DefaultContext);

  const [pz, setPz] = useState(false);
  const navigate = useNavigate();

  function handlePz() {
    setPz(true);
  }

  HeatCalc();

  return (
    <div className="final">
      <div className="final-container">
        {rRed > rObl ? (
          <div className="final rounded shadow p-2 mb-2 bg-body-tertiary w-100">
            <h4>
              R<sub>({rRed.toFixed(2)})</sub> {'>'} R<sub>({rObl.toFixed(2)})</sub>
            </h4>
            <h6> </h6>
          </div>
        ) : (
          <div className="final  rounded shadow p-2 mb-2 bg-body-tertiary w-100">
            <h4>
              R<sub>({rRed.toFixed(2)})</sub> {'<'} R<sub>({rObl.toFixed(2)})</sub>
            </h4>
            <Row className="mx-auto">
              <Col xs={7} className="mt-2">
                <p>  :</p>
              </Col>
              {secondIns ? (
                <Col xs={4}>
                  <Form.Control
                    className="w-25"
                    placeholder=" , "
                    value={insThickness ? insThickness : null}
                    onChange={handleInsThickness}
                  ></Form.Control>
                  <Form.Control
                    className="w-25"
                    placeholder=" , "
                    value={secondInsThickness ? secondInsThickness : null}
                    onChange={handleSecondInsThickness}
                  ></Form.Control>
                </Col>
              ) : (
                <Col xs={3}>
                  <Form.Control
                    placeholder=""
                    value={insThickness ? insThickness : null}
                    onChange={handleInsThickness}
                  ></Form.Control>
                </Col>
              )}
            </Row>
          </div>
        )}
        {vaporCalc &&
          (eGap < outE ? (
            <div class="final rounded shadow p-2 mb-2 bg-body-tertiary w-100">
              <h4>
                e<sub>({eGap.toFixed(0)})</sub> {'<'} E<sub>({outE.toFixed(0)})</sub>
              </h4>
              <h6> </h6>
            </div>
          ) : (
            <div className="final mt-1 rounded shadow p-3 mb-2 bg-body-tertiary ">
              <h4>
                e<sub>({eGap.toFixed(0)})</sub> {'>'} E<sub>({outE.toFixed(0)})</sub>
              </h4>
              <Row className="ms-auto">
                <Col xs={7} className="mt-2">
                  <p>   : </p>
                </Col>
                <Col xs={2}>
                  <Form.Control
                    className=" ms-4"
                    placeholder=""
                    value={ventHeight}
                    onChange={handleVentHeight}
                  ></Form.Control>
                </Col>
              </Row>
              <Row>
                <Col xs={14} className="mt-2">
                  <Form.Label>     /  </Form.Label>
                </Col>
              </Row>
              <Row>
                <Col xs={3}>
                  <Form.Control placeholder=", " value={ventIn * 1000} onChange={handleVentIn}></Form.Control>
                </Col>
                <Col xs={3} className="mx-auto">
                  <Form.Control placeholder=", " value={ventMed * 1000} onChange={handleVentMed}></Form.Control>
                </Col>
                <Col xs={3}>
                  <Form.Control placeholder=", " value={ventOut * 1000} onChange={handleVentOut}></Form.Control>
                </Col>
              </Row>
            </div>
          ))}
        {vaporCalc &&
          (gU < gObl ? (
            <div class="final rounded shadow p-2 mb-2 bg-body-tertiary w-100">
              <h4>
                G<sub>({gU.toFixed(3)})</sub> {'<'} G<sub>({gObl.toFixed(3)})</sub>
              </h4>
              <h6> </h6>
            </div>
          ) : (
            <div className="final rounded shadow p-3 mb-5 bg-body-tertiary ">
              <h4>
                G<sub>({gU.toFixed(3)})</sub> {'>'} G<sub>({gObl.toFixed(3)})</sub>
              </h4>
              <p>   </p>
              {plaster ? null : (
                <Row>
                  <Col>
                    <Form.Select id="plaster" className=" mx-auto" onChange={handlePlasterValue}>
                      <option> </option>
                      <option value={1}></option>
                      <option value={2}></option>
                      <option value={3}></option>
                    </Form.Select>
                  </Col>
                </Row>
              )}
              <Row>
                <Col xs={11}>
                  <Form.Control
                    id="vapor-membrane-air"
                    placeholder=" , /"
                    value={vaporMembraneAir}
                    onChange={handleVaporMembraneAir}
                  />
                </Col>
                <Col xs={11}>
                  <Form.Control
                    id="vapor-membrane-r"
                    placeholder=" , /"
                    value={vaporMembraneR}
                    onChange={handleVaporMembraneR}
                  />
                </Col>
              </Row>
            </div>
          ))}
      </div>
      {vaporCalc ? (
        eGap < outE && gU < gObl ? (
          <Button
            variant="outline-secondary"
            size="sm"
            className="mt-3 mb-3"
            onClick={() => {
              handlePz();
            }}
          >
             
          </Button>
        ) : null
      ) : (
        <Button
          variant="outline-secondary"
          size="sm"
          className="mt-3 mb-3"
          onClick={() => {
            handlePz();
          }}
        >
           
        </Button>
      )}
      {/* {pz && <Pz setRRed={handleRRed} setRObl={handleRObl} />} */}
      {pz && <HeatCalc />}
      <Button
        className="mb-5"
        variant="outline-secondary"
        size="sm"
        onClick={() => {
          navigate(vaporCalc || airCalc ? '/coverdata' : '/bracketdata');
        }}
      >
        
      </Button>
    </div>
  );
}
