import React, { useContext, useRef } from 'react';
import { DefaultContext } from '../contexts/DefaultContext';
import { Button, Image } from 'react-bootstrap';
import { useReactToPrint } from 'react-to-print';
import VaporCalc from './VaporCalc';
import RTable from './RTable';
import RTable2 from './RTable2';
import RTable3 from './RTable3';
import grib1 from '../images/1.jpg';
import grib2 from '../images/2.jpg';
import grib3 from '../images/3.jpg';
import grib4 from '../images/4.jpg';
import grib5 from '../images/5.jpg';
import grib6 from '../images/6.jpg';
import grib7 from '../images/7.jpg';
import grib8 from '../images/8.jpg';
import win01 from '../images/01.jpg';
import win02 from '../images/02.jpg';
import win03 from '../images/03.jpg';
import win11 from '../images/11.jpg';
import win12 from '../images/12.jpg';
import win13 from '../images/13.jpg';
import win21 from '../images/21.jpg';
import win22 from '../images/22.jpg';
import win23 from '../images/23.jpg';

export default function HeatCalc() {
  const {
    bracketResult,
    brickAir,
    brickArea,
    brickDensity,
    brickLambda,
    brickName,
    brickThickness,
    brickType,
    brickVapor,
    buildingAim,
    buildingType,
    cityProp,
    concreteAir,
    concreteArea,
    concreteLambda,
    concreteThickness,
    concreteVapor,
    concreteWall,
    coverName,
    coverThickness,
    grib,
    gribDepth,
    gribConcretePcs,
    gribPcs,
    height,
    handleRObl,
    handleRRed,
    humidity,
    innerTemp,
    insAir,
    insDensity,
    insLambda,
    insName,
    insThickness,
    insSp,
    insVapor,
    mr,
    objAddress,
    objName,
    plaster,
    secondIns,
    secondInsAir,
    secondInsDensity,
    secondInsLambda,
    secondInsThickness,
    vaporCalc,
    ventHeight,
    ventIn,
    ventMed,
    ventOut,
    windowBrickLength,
    windowConcreteLength,
    windowDepth,
    windowHeight,
    windowLoss,
    windowLossConcrete,
  } = useContext(DefaultContext);

  const brickThick = brickThickness * 0.001;
  const concreteThick = concreteThickness * 0.001;
  const insThick = insThickness * 0.001;
  const secondInsThick = secondInsThickness * 0.001;

  const brickS = brickArea && parseFloat(brickArea);
  const concreteS = concreteArea && parseFloat(concreteArea);

  const k = () => {
    if (buildingType === 1) return 1.55;
    if (brickType) return 1.3;
    if (buildingType !== 1 && !brickType) return 1.45;
  };

  const b = () => {
    if (buildingAim === 1) return 1.4;
    if (buildingAim === 2) return 1.2;
    if (buildingAim === 3) return 1;
  };
  const a = () => {
    if (buildingAim === 1) return 0.00035;
    if (buildingAim === 2) return 0.0003;
    if (buildingAim === 3) return 0.0002;
  };
  const gsop = buildingAim === 1 ? (innerTemp - cityProp.t10) * cityProp.z10 : (innerTemp - cityProp.t8) * cityProp.z8;

  const concreteQ = buildingType !== 3 && parseFloat((concreteThick / concreteLambda).toFixed(3));
  const brickQ = buildingType !== 1 && parseFloat((brickThick / brickLambda).toFixed(3));
  const insQ = parseFloat((insThick / insLambda).toFixed(3));
  const secondInsQ = secondIns && parseFloat((secondInsThick / secondInsLambda).toFixed(3));

  const rObl = (a() * gsop + b()) * mr;
  const preIns =
    (k() * rObl - concreteQ - brickQ - 1 / 8.7 - 1 / 12) *
    (secondIns
      ? (insThick / (insThick + secondInsThick)) * insLambda +
        (secondInsThick / (insThick + secondInsThick)) * secondInsLambda
      : insLambda);

  const linearLoss =
    !concreteWall &&
    parseFloat(
      buildingType !== 1 ? windowLoss * windowBrickLength : (windowLoss * windowConcreteLength) / (concreteS + brickS)
    );

  const linearLoss1 = concreteWall && parseFloat((windowLossConcrete * windowConcreteLength) / concreteS);

  const linearLoss2 =
    concreteWall &&
    parseFloat(
      (windowLoss * windowBrickLength) / (brickS + concreteS) +
        (windowLossConcrete * windowConcreteLength) / (brickS + concreteS)
    );

  console.log(bracketResult);

  const pointLoss = () => {
    let totalLoss = 0;

    for (const key in bracketResult) {
      const bracketData = bracketResult[key];
      totalLoss += (parseFloat(bracketData.value) * parseInt(bracketData.pcs)) / (concreteS + brickS);
    }
    return totalLoss;
  };

  const pointLoss1 = () => {
    let totalLoss = 0;

    for (const key in bracketResult) {
      const bracketData = bracketResult[key];
      totalLoss += bracketData.wall && (parseFloat(bracketData.value) * parseInt(bracketData.pcs)) / concreteS;
    }
    return totalLoss;
  };

  const pointLoss2 = () => {
    let totalLoss = 0;

    for (const key in bracketResult) {
      const bracketData = bracketResult[key];
      totalLoss += !bracketData.wall && (parseFloat(bracketData.value) * parseInt(bracketData.pcs)) / brickS;
    }
    return totalLoss;
  };

  const rCond1 = 1 / 8.7 + concreteQ + insQ + secondInsQ + 1 / 12;
  const rCond2 = 1 / 8.7 + brickQ + insQ + secondInsQ + 1 / 12;

  const u1 = 1 / rCond1;
  const u2 = 1 / rCond2;

  const rRed =
    1 /
    ((buildingType !== 3 && u1 * (concreteS / (brickS + concreteS))) +
      (buildingType !== 1 && u2 * (brickS / (brickS + concreteS))) +
      linearLoss +
      pointLoss() +
      gribPcs * gribDepth);

  const rRed1 = 1 / (u1 + linearLoss1 + pointLoss1() + gribConcretePcs * gribDepth);

  const rRed2 = 1 / (u2 + linearLoss2 + pointLoss2() + gribPcs * gribDepth);

  const r = rRed / rCond2;

  const r1 = rRed1 / rCond1;

  const r2 = rRed2 / rCond2;

  const bracketDensity = () => {
    let brackets = 0;
    for (const key in bracketResult) {
      const bracketData = bracketResult[key];
      brackets += parseInt(bracketData.pcs);
    }
    return brackets / (brickS + concreteS);
  };

  const brackets = () =>
    Object.entries(bracketResult).map(([key, item]) => (
      <React.Fragment key={key}>
        <br /> - {item.type ? '' : ''} {item.weight ? '' : ''} {' '}
        {item.bracket} - {(item.pcs / (concreteS + brickS)).toFixed(1)} /
      </React.Fragment>
    ));
  const brackets2 = () =>
    Object.entries(bracketResult).map(([key, item]) => (
      <React.Fragment key={key}>
        <br /> <sup>{parseInt(key) + 1}</sup> = {item.value.toFixed(3)} /   {item.bracket}{' '}
        {buildingType === 2 && ` : ${item.wall ? '' : '/'}`};
      </React.Fragment>
    ));
  const brackets3 = () =>
    Object.entries(bracketResult).map(([key, item]) => (
      <React.Fragment key={key}>
        <br /> -  {item.bracket} (  {parseInt(key) + 2})
      </React.Fragment>
    ));

  const windowD = () => {
    if (windowDepth === '1') return '  ,     100 ';
    if (windowDepth === '2') return '     ';
    if (windowDepth === '3') return '  ,     100 ';
  };

  const windowH = () => {
    if (windowHeight === '1') return '    ';
    if (windowHeight === '2') return 'c     20 ';
    if (windowHeight === '3') return 'c     60 ';
  };

  const contentToPrint = useRef(null);
  const handlePrint = useReactToPrint({
    content: () => contentToPrint.current,
    removeAfterPrint: true,
  });

  concreteWall ? handleRRed(rRed1) : handleRRed(rRed);
  handleRObl(rObl);
  vaporCalc &&
    VaporCalc({
      brickQ: brickQ,
      concreteQ: concreteQ,
      insQ: insQ,
      rCond1: rCond1,
      rCond2: rCond2,
      rRed: rRed2,
      secondInsQ: secondInsQ,
    });
  const windowPic = () => {
    if (windowDepth == 1 && windowHeight == 1) return win01;
    if (windowDepth == 1 && windowHeight == 2) return win02;
    if (windowDepth == 1 && windowHeight == 3) return win03;
    if (windowDepth == 2 && windowHeight == 1) return win11;
    if (windowDepth == 2 && windowHeight == 2) return win12;
    if (windowDepth == 2 && windowHeight == 3) return win13;
    if (windowDepth == 3 && windowHeight == 1) return win21;
    if (windowDepth == 3 && windowHeight == 2) return win22;
    if (windowDepth == 3 && windowHeight == 3) return win23;
  };

  const gribPic = () => {
    if (gribDepth == 0.006) return grib1;
    if (gribDepth == 0.005) return grib2;
    if (gribDepth == 0.004) return grib3;
    if (gribDepth == 0.003) return grib4;
    if (gribDepth == 0.0025) return grib5;
    if (gribDepth == 0.002) return grib6;
    if (gribDepth == 0.0015) return grib7;
    if (gribDepth == 0.001) return grib8;
  };

  return (
    <>
      <div ref={contentToPrint} className="p-5 position-relative">
        <div className="d-none">
          <div className=" stamp-body"></div>
          <div className="stamp1"></div>
          <div className="stamp2"></div>
          <div className="stamp3"></div>
          <div className="stamp4"></div>
          <div className="stamp5"></div>
          <div className="stamp6"></div>
          <div className="stamp7"></div>
          <div className="stamp8"></div>
          <div className="stamp9"></div>
        </div>
        <h4>         </h4>
        <h4>
           : {objName},    : {objAddress}
        </h4>
        <br />
        <h5>1.   .</h5>
        <div>
          <b>   :</b> <br />
               3.1  131.13330.2020   5.7  50.13330.2012; <br />-
              ,   0,92: t = {cityProp.t} ;
          <br />-     : t<sub></sub> = {cityProp.tm} ; <br />- 
            : t<sub>{buildingAim === 1 ? '10' : '8'}</sub> ={' '}
          {buildingAim === 1 ? cityProp.t10 : cityProp.t8} ;
          <br />-   : z<sub>{buildingAim === 1 ? '10' : '8'}</sub> ={' '}
          {buildingAim === 1 ? cityProp.z10 : cityProp.z8} ;
          <br />-        :  = {cityProp.v} /c;
          <br />-     :  = {humidity} %; <br />
          <br />
          <b>  :</b> <br /> -    : t<sub></sub>= {innerTemp}{' '}
          ; <br /> -        : <sub></sub> ={' '}
          {cityProp.w} %.
          <br />
          <br /> <b> :</b>
          <br />
          {plaster &&
            `- ${
              plaster === 2 ? '' : ''
            }    15    .`}
          {buildingType !== 3 &&
            (buildingType === 2 ? (
              concreteWall ? (
                <div>-    {concreteThickness} ;</div>
              ) : (
                <div>
                  -  ,       {' '}
                  {brickThickness} ;
                </div>
              )
            ) : (
              <div>-    {concreteThickness} ;</div>
            ))}
          {buildingType !== 1 && (
            <div>
              - {brickName}  {brickDensity} /,  {brickThickness} ;
            </div>
          )}
          {secondIns ? (
            <div>
              -     {insDensity} /,  {insThickness} ; <br />- 
                 {secondInsDensity} /,  {secondInsThickness} ;
            </div>
          ) : (
            <div>
              -  {insName} {insSp && <> {insDensity} /, </>} {insThickness} ;
            </div>
          )}
          <br />
          <b>  :</b> <br />
          {buildingType !== 3 && (
            <div>
              {' '}
               : <br />-     = {concreteLambda} /();
              <br />-     = {concreteVapor} /();
              <br /> -    = {concreteAir} /();
            </div>
          )}
          {buildingType !== 1 && (
            <div>
              {' '}
               :
              <br /> -     = {brickLambda} /();
              <br /> -     = {brickVapor} /();
              <br /> -    = {brickAir} /();
            </div>
          )}
          <>
            {secondIns ? '  ' : ''} :
            <br /> -     = {insLambda} /();
            <br /> -     = {insVapor} /();
            <br />-    = {insAir} /();
            <br />
          </>
          {secondIns && (
            <>
                :
              <br /> -     = {secondInsLambda} /();
              <br /> -     = {insVapor} /();
              <br />-    = {secondInsAir} /();
            </>
          )}
          {plaster !== 1 && (
            <>
               :
              <br /> -     = {plaster === 3 ? '0.93' : '0.81'} /();
              <br /> -     = {plaster === 3 ? '0.09' : '0.12'} /();
              <br /> -    = {plaster === 3 ? '0.00004' : '0.00004'} /();
              <br />
            </>
          )}
          <br /> <b>  :</b>
          <br />
          {vaporCalc && (
            <>
                H = {height} 
              <br />
                   <sub></sub> = {ventIn * 1000}  <br />
                   <sub></sub> = {ventOut * 1000} 
              <br />
                  <sub></sub> = {ventMed * 1000} 
              <br />
                   h = {ventHeight}  <br />
              <br />
            </>
          )}
               {bracketDensity().toFixed(1)} /  :
          {brackets()}
          <br />
          <br />         {concreteWall &&
            ' '}: {gribPcs} /
          <br />{' '}
          {concreteWall && (
            <>
                       : {gribConcretePcs}{' '}
              /
              <br />
            </>
          )}{' '}
          {vaporCalc && (
            <>
              <br />
               - {coverName}  {coverThickness} 
            </>
          )}
          <br /> <h5>2.   .</h5>
          -      :  = (t<sub></sub> - t
          {buildingAim === 1 ? <sub>10</sub> : <sub>8</sub>})  z{buildingAim === 1 ? <sub>10</sub> : <sub>8</sub>}= (
          {innerTemp} -(
          {buildingAim === 1 ? cityProp.t10 : cityProp.t8}))  {buildingAim === 1 ? cityProp.z10 : cityProp.z8}={' '}
          {gsop.toFixed(0)}
          . <br />t<sub></sub> -   
          <br />t{buildingAim === 1 ? <sub>10</sub> : <sub>8</sub>} -      .
          3.1  131.13330.2020
          <br />z{buildingAim === 1 ? <sub>10</sub> : <sub>8</sub>} -     .
          3.1  131.13330.2020
          <br />
                  50.13330.2012 : <br />R
          <sub></sub> = (a   + b)  m<sub>r</sub> = ({a()}  {gsop.toFixed(0)} + {b()})  {mr} ={' '}
          {rObl.toFixed(2)} /.
          <br />a -   . 3  50.13330.2012
          <br />b -   . 3  50.13330.2012
          <br /> <br />
          <h5>3.    .</h5>
             :  = (k  R<sub></sub> - <sub></sub>/<sub></sub> - 1/<sub></sub> -
          1/<sub></sub>)  <sub></sub> = ({k()}  {rObl.toFixed(2)}
          {concreteQ && ` - ${concreteQ}`}
          {brickQ && ` - ${brickQ}`} - 1 / 8.7 - 1 / 12)  (
          {secondIns
            ? (insThick / (insThick + secondInsThick)) * insLambda +
              (secondInsThick / (insThick + secondInsThick)) * secondInsLambda
            : insLambda}
          ) = {preIns.toFixed(2) * 1000} ,
          <br /><sub></sub> = 8,7  . 4  50.13330.2012
          <br /><sub></sub> = 12  . 6  50.13330.2012
          <br />k = {k()}      
          <br /> <sub></sub> -   
          <br /> <sub></sub> -     
          <br /> <sub></sub> -   
          <br />
          <br />
                        12250(2020) 
          09  2020 .    .71, .73, .74, .75  230.1325800.2015 :
          {brackets2()}
          <br />             
             {grib} .
          <Image src={gribPic()} alt="a" className="systdata-grib-img d-flex" />
          <br />     4  230.1325800.2015       ={' '}
          {gribDepth} /.
          <h5>
            <br />
            4.             50.13330.2012.
          </h5>
                     
                .     :
          <br />
          {buildingType === 2 ? (
            <>
              -      (  1)
              <br /> -      (  2)
            </>
          ) : (
            <>-     (  1)</>
          )}
          <br />
          {buildingType !== 2 ? (
            '-   (  1)'
          ) : (
            <>
              -  ,      (  1)
              <br /> -  ,       (  2)
            </>
          )}
          <br /> -  ,     (  1).
          {brackets3()}
          <br />
          <br /> <b>   .</b>
          <br />        R :  ={' '}
          {concreteS + brickS} ;
          <br />
          {buildingType === 2 ? (
            <>
              {concreteWall ? (
                <>
                        : {concreteS}      
                    {brickS} .     ,  ,
                        : {windowConcreteLength} .
                  <br />
                    ,   1     l = {windowConcreteLength} /
                  {concreteS} = {(windowConcreteLength / concreteS).toFixed(2)} /.
                  <br />     ,  ,   
                     : {windowBrickLength} .
                  <br />
                    ,   1     l = {windowBrickLength} /{brickS}{' '}
                  = {(windowBrickLength / brickS).toFixed(2)} /.
                </>
              ) : (
                <>
                         (..    
                  )  {concreteS} . <br />        
                     ={concreteS}/{concreteS + brickS} ={' '}
                  {(concreteS / (concreteS + brickS)).toFixed(2)}.<br />
                         {brickS} .
                  <br />            ={brickS}/
                  {concreteS + brickS} = {(brickS / (concreteS + brickS)).toFixed(2)}.
                  <br />
                      ,  ,    
                    : {windowBrickLength} .
                  <br />
                    ,   1     l = {windowBrickLength} /
                  {concreteS + brickS} = {(windowBrickLength / (concreteS + brickS)).toFixed(2)} /.
                </>
              )}
            </>
          ) : (
            <>
                : {buildingType === 1 ? concreteS : brickS} 
              <br />          :  ={' '}
              {buildingType === 1 ? concreteS : brickS}/{concreteS + brickS} ={' '}
              {((buildingType === 1 ? concreteS : brickS) / (concreteS + brickS)).toFixed(2)}
              <br />
                  ,  ,      
              : {buildingType === 1 ? windowConcreteLength : windowBrickLength} .
              <br />
                ,   1     l ={' '}
              {buildingType === 1 ? windowConcreteLength : windowBrickLength} /{concreteS + brickS} ={' '}
              {((buildingType === 1 ? windowConcreteLength : windowBrickLength) / (concreteS + brickS)).toFixed(2)}{' '}
              /.
            </>
          )}
          <br />
          <br /> <b>   ,  .</b>
          <br />    {buildingType === 2 && 1}       .6,
          .  50.13330.2012:
          <br />R<sub>{buildingType === 2 && 1}</sub>
          <sup></sup> = 1/<sub></sub> + R
          <sub>s</sub> + 1/<sub>0</sub> = 1/8.7 + {buildingType !== 3 ? concreteQ : brickQ} +{' '}
          {secondIns ? `${insQ} + ${secondInsQ}` : insQ}+ 1/12 ={' '}
          {buildingType !== 3 ? rCond1.toFixed(3) : rCond2.toFixed(2)} /
          <br />U<sub>{buildingType === 2 && 1}</sub> = 1/R<sub>{buildingType === 2 && 1}</sub>
          <sup></sup> = 1/ {buildingType !== 3 ? rCond1.toFixed(2) : rCond2.toFixed(2)} ={' '}
          {buildingType !== 3 ? u1.toFixed(3) : u2.toFixed(3)} /()
          <br />
          {buildingType === 2 && (
            <>
                 2     : <br />R<sub>2</sub>
              <sup></sup> = 1/8.7 + {brickQ} + {secondIns ? `${insQ}+${secondInsQ}` : `${insQ}`} + 1/12 ={' '}
              {rCond2.toFixed(2)} C/
              <br />U<sub>2</sub> = 1/ {rCond2.toFixed(2)} = {u2.toFixed(2)} /()
            </>
          )}
          <br />
                     .33   
          230.1325800.2015 {windowD()}  {windowH()}.
          <br />
          <Image src={windowPic()} alt="a" className="systdata-window-img " />
          <br />
                  {' '}
          {concreteWall ? '2 ' : ''}     50.13330.2012,   
               .
        </div>
        {!concreteWall && (
          <>
            <RTable brickS={brickS} concreteS={concreteS} u1={u1} u2={u2} />   
              : R<sub></sub> = {rRed.toFixed(2)} /
            <br />
                 : r = R<sub></sub>/R<sub></sub> ={' '}
            {rRed.toFixed(2)}/{buildingType === 1 ? rCond1.toFixed(2) : rCond2.toFixed(2)} = {r.toFixed(2)}
            <br />
          </>
        )}
        {concreteWall && (
          <>
            <RTable2 concreteS={concreteS} u1={u1} />
                 : R<sub></sub> = {rRed1.toFixed(2)} /
            <br />
                 : r = R<sub></sub>/R<sub></sub> ={' '}
            {rRed1.toFixed(2)}/{rCond1.toFixed(2)} = {r1.toFixed(2)}
            <br />
            <RTable3 brickS={brickS} u2={u2} />
                 : R<sub></sub> = {rRed2.toFixed(2)}
            <br />
                 : r = R<sub></sub>/R<sub></sub> ={' '}
            {rRed2.toFixed(2)}/{rCond2.toFixed(2)} = {r2.toFixed(2)}
          </>
        )}
        <br />
        <br />
        {vaporCalc && (
          <VaporCalc
            brickQ={brickQ}
            concreteQ={concreteQ}
            insQ={insQ}
            rCond1={rCond1}
            rCond2={rCond2}
            secondInsQ={secondInsQ}
          />
        )}
        <b>:</b>{' '}
        {rRed > rObl
          ? `    ${objName}  : ${objAddress}  
          ${concreteWall ? rRed1.toFixed(2) : rRed.toFixed(2)} /  
          -    ,  ${rObl.toFixed(2)} 
        /.)`
          : `    ${objName}  : ${objAddress}  
          ${
          concreteWall ? rRed1.toFixed(2) : rRed.toFixed(2)
        } /   
          -    ,  ${rObl.toFixed(2)} 
        /.`}
        <br />
      </div>
      <Button
        className="mt-3 mb-5"
        variant="outline-secondary"
        size="sm"
        onClick={() => {
          handlePrint();
        }}
      >
        
      </Button>
    </>
  );
}
